import React from 'react';
import View from 'react-flexbox';
import history from '../core/history';

import s from './BrowserMock.css';

const PADDING = 12;

class BrowserMock extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      hover: false,
    };
  }
  static propTypes = {
    style: React.PropTypes.object,
    pathname: React.PropTypes.string,
    children: React.PropTypes.node
  };
  onMouseOver = () => {
    this.setState({
      hover: true,
    });
  }
  onMouseLeave = () => {
    this.setState({
      hover: false,
    });
  }
  onMouseUp = () => {
    if (!this.props.pathname) {
      return;
    } else {
      window.setTimeout(() => {
        const pathname = this.props.pathname;
        history.push({
          pathname: pathname
        });
      }, 500);
    }
  }
  render() {
    const { pathname, style, ...props } = this.props;
    const browserMockStyle = {
      alignItems: 'center',
      justifyContent: 'center',
      height: '100%',
      borderTop: '2em solid rgba(230, 230, 230, 0.7)',
      boxShadow: '0 0.1em 1em 0 rgba(0, 0, 0, 0.4)',
      position: 'relative',
      borderRadius: '3px 3px 0 0',
      transition: 'all 0.4s ease-out',
    };
    const browserDotsStyle = {
      display: 'block',
      position: 'absolute',
      content: '',
      top: '-1.25em',
      left: '1em',
      width: '0.5em',
      height: '0.5em',
      borderRadius: '50%',
      backgroundColor: '#f44',
      boxShadow: '0 0 0 2px #f44, 1.5em 0 0 2px #9b3, 3em 0 0 2px #fb5',
    };
    const browserBodyStyle = {
      position: 'relative',
      height: '100%',
      minWidth: 200,
      overflowY: 'hidden',
      justifyContent: 'center',
      alignSelf: 'center',
      alignItems: 'center',
      textAlign: 'center',
      width: '100%',
      zIndex: 1000,
    };
    if (this.state.hover) {
      browserMockStyle.backgroundColor = 'rgba(0,0,0,0.25)';
      browserMockStyle.cursor = 'pointer';
    }
    const viewStyle = Object.assign({}, browserMockStyle, style);
    return (
      <View 
        {...props}
        column
        style={viewStyle}
        onMouseOver={this.onMouseOver}
        onMouseLeave={this.onMouseLeave}
        onMouseUp={this.onMouseUp}>
        <div style={browserDotsStyle}></div>
        <View column style={browserBodyStyle} className={s.ripple}>
          {this.props.children}
        </View>
      </View>
    );
  }
}

export default BrowserMock;



